<template>
  <div>
    <div class="dialog_title"></div>
    <el-divider></el-divider>
    <el-form ref="loginFormRef" :model="formData" :rules="formDataRules"
      label-width="0px" class="login_form">
      <!--  -->
      <el-form-item prop="phone">
        <el-input v-model="formData.phone" prefix-icon="el-icon-mobile-phone"
          placeholder="">
        </el-input>
      </el-form-item>
      <!--  -->
      <el-form-item>
        <el-row :gutter="10">
          <el-col :span="16">
            <el-input v-model="formData.verifycode"
              prefix-icon="el-icon-message"  placeholder="">
            </el-input>
          </el-col>
          <el-col :span="8">
            <el-button type="info" plain></el-button>
          </el-col>
        </el-row>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" class="login_btn" @click="submitForm()">
          
        </el-button>
      </el-form-item>
    </el-form>
    <el-row type="flex" justify="space-between" class="jump_link">
      <el-col :span="12">
        <el-link type="primary" @click="toLogin()">>>></el-link>
      </el-col>
    </el-row>
  </div>
</template>

<script>
export default {
  data () {
    var checkPhone = (rule, value, callback) => {
      if (!value) {
        return callback(new Error(''))
      } else {
        const reg = /^1[3|4|5|7|8][0-9]\d{8}$/
        if (reg.test(value)) {
          callback()
        } else {
          return callback(new Error(''))
        }
      }
    }
    return {
      formData: {
        phone: '',
        verifycode: ''
      },
      formDataRules: {
        phone: [
          { required: true, message: '', trigger: 'blur' },
          { validator: checkPhone, trigger: ['blur', 'change'] }
        ],
        password: [
          { required: true, message: '', trigger: 'blur' },
          { min: 6, max: 11, message: ' 6  11 ', trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    toLogin () {
      this.$emit('getMessage', '110px')
      this.$router.push('/login')
    },
    submitForm () {
      this.$router.push('/restPwd')
    }
  }
}
</script>

