<template>
  <el-container>
    <el-container>
      <el-main>
        <p>{{this.showData.stockName}}{{this.showData.head}}</p>
      </el-main>
      <el-footer height="auto">
        <el-row class="detailInfo">
          <el-col :span="12">{{this.showData.author}} {{this.showData.date}}</el-col>
          <el-col :span="12" style="text-align: end;">#{{this.showData.tag}} {{this.showData.stockName}}</el-col>
        </el-row>
      </el-footer>
    </el-container>
    <el-footer>
      <div style="white-space: pre-wrap; margin: 5px 0 5px 0">{{this.showData.text}}</div>
    </el-footer>
    <div class="backBtn">
      <el-button circle plain type="danger" @click="goClick"><i class="el-icon-back"></i></el-button>
    </div>
  </el-container>
</template>

<script>
export default {
  created () {
    this.infoid = this.$route.query.infoid
    this.getInfos()
  },
  data () {
    return {
      infoid: 1,
      showData: {
        // author: '',
        // date: '2019-06-04 00:00:00',
        // head: '500',
        // stockName: '',
        // tag: '',
        // text: '\t10\n' +

        //       '\t\n' +

        //       '\t+\n' +

        //       '\t1118\n' +

        //       '\t\n' +

        //       '\t11273.36.2%11.1%10\n' +

        //       '\t\n'
      },
    }
  },
  methods: {
    async getInfos () {
      const { data: res } = await this.$http.get('infos/detail', { params: {infoid:this.infoid} })
      if (res.meta.status !== 200) {
          return this.$message.error('')
      }
      this.showData = res.data
      console.log(res)
      // return this.$message.success('')
    },
    goClick () {
      this.$router.push({
        path:'/infoList',
        query: {
          back: true
        }
      })
    }
  }
}
</script>

<style lang="less" scoped>
.el-main
{
  padding: 10px 0 10px 18px;
}
.el-main p
{
  font-size: large;
  line-height: 0px;
}
.detailInfo
{
  font-size: 13px;
  color: #b1b1b1;
}
.backBtn
{
  position: fixed;
  bottom: 12.5%;
  font-size: 19px;
  width:985px;
  text-align:end;
}
</style>
