/**
 * 
 * @param {Object} price 
 * @param {Object} yclose 
 */
export function ratioCalculate(price, yclose) {
  return (((price - yclose) / yclose) * 100).toFixed(3)
}

//
export function splitData(rawData) {
  let datas = []
  let times = []
  let vols = []
  for (let i = 0; i < rawData.length; i++) {
    datas.push(rawData[i])
    times.push(rawData[i].splice(0, 1)[0])
    vols.push(rawData[i][4])
  }
  return { datas: datas, times: times, vols: vols }
}

//Kdj
export function splitKdjData(rawData) {
  let categoryData = []
  let k = []
  let d = []
  let j = []
  for (let i = 0; i < rawData.length; i++) {
    categoryData.push(rawData[i].date)
    k.push(rawData[i].k)
    d.push(rawData[i].d)
    j.push(rawData[i].j)
  }
  return {
    categoryData: categoryData,
    k: k,
    d: d,
    j: j
  }
}

export function calculateMA(dayCount, data) {
  let result = []
  for (let i = 0, len = data.times.length; i < len; i++) {
    if (i < dayCount) {
      result.push('-')
      continue
    }
    let sum = 0
    for (let j = 0; j < dayCount; j++) {
      sum += data.datas[i - j][1]
    }
    result.push((sum / dayCount).toFixed(2))
  }
  return result
}

/*
 * EMAMACD
 * @param {number} n 
 * @param {array} data 
 * @param {string} field 
 */
function calcEMA(n, data, field) {
  let i, l, ema, a
  a = 2 / (n + 1)
  if (field) {
    //
    ema = [data[0][field]]
    for (i = 1, l = data.length; i < l; i++) {
      ema.push((a * data[i][field] + (1 - a) * ema[i - 1]).toFixed(2))
    }
  } else {
    //
    ema = [data[0]]
    for (i = 1, l = data.length; i < l; i++) {
      ema.push((a * data[i] + (1 - a) * ema[i - 1]).toFixed(3))
    }
  }
  return ema
}

/*
 * DIFMACD
 * @param {number} short EMA
 * @param {number} long EMA
 * @param {array} data 
 * @param {string} field 
 */
function calcDIF(short, long, data, field) {
  let i, l, dif, emaShort, emaLong
  dif = []
  emaShort = calcEMA(short, data, field)
  emaLong = calcEMA(long, data, field)
  for (i = 0, l = data.length; i < l; i++) {
    dif.push((emaShort[i] - emaLong[i]).toFixed(3))
  }
  return dif
}

/*
 * DEAMACD
 * @param {number} mid dif
 * @param {array} dif 
 */
function calcDEA(mid, dif) {
  return calcEMA(mid, dif)
}

/*
 * MACD
 * @param {number} short EMA
 * @param {number} long EMA
 * @param {number} mid dea
 * @param {array} data 
 * @param {string} field 
 */
export function calcMACD(short, long, mid, data, field) {
  let i, l, dif, dea, macd, result
  result = {}
  macd = []
  dif = calcDIF(short, long, data, field)
  dea = calcDEA(mid, dif)
  for (i = 0, l = data.length; i < l; i++) {
    macd.push(((dif[i] - dea[i]) * 2).toFixed(3))
  }
  result.dif = dif
  result.dea = dea
  result.macd = macd
  return result
}
